import React, { useEffect, useRef } from 'react';
import './index.css'
import * as dat from 'dat.gui'
import io from 'socket.io-client'

let host = '192.168.1.4'
// host = 'localhost'
const socket = io(`${host}:4400`)

function App() {
  const wrapper = useRef()
  const gui = new dat.GUI({ autoPlace: false, width: window.innerWidth > 360 ? 360 : window.innerWidth - 50 })
  var parameters = {
    zoom: 1,
    color: '#FFFFFF',
    color1: '#FF0000',
    r: 0,
    g: 0,
    b: 0
  }

  const sendIt = (z) => {
    socket.emit('change_z', z)
  }

  const changeColor = (c) => socket.emit('change_color', c)
  const changeColor1 = (c) => socket.emit('change_color1', c)


  const sendR = (r) => socket.emit('r', r)
  const sendG = (g) => socket.emit('g', g)
  const sendB = (b) => socket.emit('b', b)


  gui.add(parameters, 'zoom', 0, 1000).onChange(z => sendIt(z))
  gui.add(parameters, 'r', 0, 1, .01).onChange(r => sendR(r))

  gui.add(parameters, 'g', 0, 1, .01).onChange(g => sendG(g))
  gui.add(parameters, 'b', 0, 1, .01).onChange(b => sendB(b))

  gui.addColor(parameters, 'color').onChange(c => changeColor(c))
  gui.addColor(parameters, 'color1').onChange(c => changeColor1(c))


  useEffect(() => {
    wrapper.current.appendChild(gui.domElement);
  })
  return (<div style={{ display: 'block', width: '90%', margin: 'auto' }}><div style={{ margin: 'auto' }} ref={wrapper}></div></div>
  );
}

export default App;
